@value makerBlue, makerRed, makerBlack from "../../common/colors.css";
@value mediaQuery from "../../common/size.css";

.displayBox {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  padding: 12px;
  margin-bottom: 10px;
  width: 100%;
  font-size: 1em;
  font-weight: 700;
  color: makerBlack;
  border: 4px solid makerBlack;
}

.gameOverBox {
  background-color: makerBlack;
}

.span {
  display: flex;
  justify-content: flex-start;
}

.gameOver {
  justify-content: center;
  text-shadow: 0 0 5px #ffa500, 0 0 15px #ffa500, 0 0 20px #ffa500,
    0 0 40px #ffa500, 0 0 60px #ff0000, 0 0 10px #ff8d00, 0 0 98px #ff0000;
  color: #fff6a9;
  text-align: center;
  animation: blink 12s infinite;
}

@keyframes blink {
  20%,
  24%,
  55% {
    color: #111;
    text-shadow: none;
  }
  0%,
  19%,
  21%,
  23%,
  25%,
  54%,
  56%,
  100% {
    text-shadow: 0 0 5px #ffa500, 0 0 15px #ffa500, 0 0 20px #ffa500,
      0 0 40px #ffa500, 0 0 60px #ff0000, 0 0 10px #ff8d00, 0 0 98px #ff0000;
    color: #fff6a9;
  }
}

@media screen and (max-width: mediaQuery) {
  .displayBox {
    padding: 5px;
    font-size: 10px;
  }
}
